	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Fried Fish","date":"29 Mar 2007","slug":"/2007-03-29-fried-fish/","tags":"Y2007"},"html":"<p>Thanks to <a href=\"http://burningbird.net/cephalopods/three-for-the-cephalopods/\">Shelley for pointing</a> to this <a href=\"http://cephalopodcast.com/blog/2007/03/28/weird-aquaria-fast-food-fryer-live-goldfish-tank/\">combination goldfish tank and fast food fryer</a>. But I doubt that our field consultant would approve it.</p>\n<blockquote><p>\n  Because oil floats on water, despite the massive heat (163 degrees Celsius) the goldfish simply stay away from the surface and all is well. They eat the crumbs of croquettes and other fried foods that fall to the bottom, and can live in there for 5-10 years as they happily clean away, ignorant to the fact that certain death awaits any potential escapees.\n</p></blockquote>\n<p><object width=\"425\" height=\"350\"><param name=\"movie\" value=\"http://www.youtube.com/v/ZH96MhITOlk\" /><embed src=\"http://www.youtube.com/v/ZH96MhITOlk\" type=\"application/x-shockwave-flash\" width=\"425\" height=\"350\"></embed></object></p>"},"next":{"fields":{"title":"Hamburg Sunny Day","date":"28 Mar 2007","slug":"/2007-03-28-hamburg-sunny-day/"}},"previous":{"fields":{"title":"All McDonald's in Germany Smoke-Free","date":"30 Mar 2007","slug":"/2007-03-30-all-mcdonalds-in-germany-smoke-free/"}}},{"node":{"fields":{"title":"Hamburg Sunny Day","date":"28 Mar 2007","slug":"/2007-03-28-hamburg-sunny-day/","tags":"Y2007"},"html":"<p>Despite the excitement of our upcoming business venture, we still have our usual everyday events. Right now Christopher has two weeks off school for Easter vacation, and I've taken the time off work. MamaMaus works this week, but we'll all three be taking off to Oslo and Denmark for a few days next week.</p>\n<p>Today Christopher and I took advantage of the sunny weather to head to Hamburg. After hitting the Lego store, we headed to the harbor for the cheapest harbor tour in town... the HADAG commuter ferry, all you need is a HVV subway ticket.</p>\n<p><a href=\"http://www.flickr.com/photos/papascott/437745944/\" title=\"Photo Sharing\"><img src=\"https://farm1.static.flickr.com/175/437745944_dacb71bd21.jpg\" width=\"500\" height=\"375\" alt=\"IMG 1910\" /></a></p>\n<p>We then went to the springtime <a href=\"http://www.papascott.de/archives/2000/07/23/hamburger-dom/\">Hamburger Dom</a>, Hamburg's carnival midway. If you've been to Oktoberfest, imagine the Wies'n without the obnoxious beer tents (however difficult that may be). Today was even family day, so every stand had special prices.</p>\n<p><a href=\"http://www.flickr.com/photos/papascott/437745186/\" title=\"Photo Sharing\"><img src=\"https://farm1.static.flickr.com/179/437745186_5a8bfb521f.jpg\" width=\"500\" height=\"375\" alt=\"IMG 1917\" /></a></p>\n<p>This is Christopher atop the fun house.</p>\n<p><a href=\"http://www.flickr.com/photos/papascott/437744908/\" title=\"Photo Sharing\"><img src=\"https://farm1.static.flickr.com/154/437744908_a5c34547ef.jpg\" width=\"375\" height=\"500\" alt=\"IMG 1921\" /></a></p>\n<p>This is how he landed.</p>"},"next":{"fields":{"title":"Plus Twenty Years","date":"27 Mar 2007","slug":"/2007-03-27-plus-twenty-years/"}},"previous":{"fields":{"title":"Fried Fish","date":"29 Mar 2007","slug":"/2007-03-29-fried-fish/"}}},{"node":{"fields":{"title":"Plus Twenty Years","date":"27 Mar 2007","slug":"/2007-03-27-plus-twenty-years/","tags":"Y2007"},"html":"<p>We were able to announce our <a href=\"http://www.papascott.de/archives/2007/03/27/bricks-and-mortar/\">big change</a> today because MamaMaus broke the news to her staff today. One of her team members told her on the phone this evening, \"I guess you'll only be around for a couple more days... plus twenty years\".</p>\n<p>That's true. The franchise contract for McDonald's runs for twenty years. By that time I'll be 65 and ready for something new.</p>\n<p>Then again, many franchise contracts are extended into <a href=\"http://www.bizjournals.com/columbus/stories/2007/03/19/focus2.html?b=1174276800%5E1432966\">\"second generation\"</a>. In twenty years Christopher will be 27...</p>"},"next":{"fields":{"title":"Bricks and Mortar","date":"27 Mar 2007","slug":"/2007-03-27-bricks-and-mortar/"}},"previous":{"fields":{"title":"Hamburg Sunny Day","date":"28 Mar 2007","slug":"/2007-03-28-hamburg-sunny-day/"}}}],"pathPrefix":"","first":false,"last":false,"index":145,"pageCount":1101}}

